import circomlib from 'circomlib';
import snarkjs from 'snarkjs';
import crypto from 'crypto';

import websnarkUtils from 'websnark/src/utils';
import axios from 'axios';

const bigInt = snarkjs.bigInt;
 
/** Generate random number of specified byte length */
const rbigint = (nbytes) => snarkjs.bigInt.leBuff2int(crypto.randomBytes(nbytes));

/** Compute pedersen hash */
const pedersenHash = (data) => circomlib.babyJub.unpackPoint(circomlib.pedersenHash.hash(data))[0];

/** BigNumber to hex string of specified length */
export function toHex(number, length = 32) {
    const str = number instanceof Buffer ? number.toString('hex') : bigInt(number).toString(16);
    return '0x' + str.padStart(length * 2, '0');
}

function toJson(data) {
  return JSON.stringify(data, (_, v) => typeof v === 'bigint' ? `${v}n` : v)
      .replace(/"(-?\d+)n"/g, (_, a) => a);
}


let deposit;
let curAmount;

function createDeposit({ nullifier, secret }) {
  // console.log('nullifier', nullifier);
  // console.log('secret', secret);
  const deposit = { nullifier, secret };
  deposit.preimage = Buffer.concat([deposit.nullifier.leInt2Buff(31), deposit.secret.leInt2Buff(31)]);
  deposit.commitment = pedersenHash(deposit.preimage);
  deposit.commitmentHex = toHex(deposit.commitment);
  deposit.nullifierHash = pedersenHash(deposit.nullifier.leInt2Buff(31));
  // console.log('nullifierHash', deposit.nullifierHash);
  deposit.nullifierHex = toHex(deposit.nullifierHash);
  return deposit;
}

export function generateNote(currency, amount, netId) {
  
  deposit = createDeposit({
    nullifier: rbigint(31),
    secret: rbigint(31)
  });
  const note = toHex(deposit.preimage, 62);
  // console.log('note', note);
  const noteString = `messier-${currency}-${amount}-${netId}-${note}`;
  // console.log(`Your note: ${noteString}`);
  // console.log('deposit.commitment', toHex(deposit.commitment));
  return noteString;
}

export function getCurrentCommitment(){
  return toHex(deposit.commitment);
}

export function getCurrentNote(){
  return {p: toJson(deposit.nullifier), q: toJson(deposit.secret)};
} 

export function getCurrentAmount(){
  return curAmount;
} 

export function parseNote(noteString) {
  const noteRegex = /messier-(?<currency>\w+)-(?<amount>[\d.]+)-(?<netId>\d+)-0x(?<note>[0-9a-fA-F]{124})/g
  const match = noteRegex.exec(noteString);
  if (!match) {
    return "Invalid";
  }
  curAmount = match.groups.amount;
  const buf = Buffer.from(match.groups.note, 'hex');
  const nullifier = bigInt.leBuff2int(buf.slice(0, 31));
  const secret = bigInt.leBuff2int(buf.slice(31, 62));
  const deposit = createDeposit({ nullifier, secret });
  const netId = Number(match.groups.netId);

  // console.log(match.groups.currency, match.groups.amount, netId, deposit);
  return {
    currency: match.groups.currency,
    amount: match.groups.amount,
    netId,
    deposit
  }
}

export async function generateProof({ deposit, currency, amount, recipient, relayerAddress = 0, fee = 0, refund = 0, poolInfos, relayerURL }) {
  // Compute merkle proof of our commitment
  const { root, pathElements, pathIndices } = poolInfos;
  // console.log('nullifierHash', deposit.nullifierHash);
  // Prepare circuit input
  // const input = {
  //   // Public snark inputs
  //   root: root,
  //   nullifierHash: deposit.nullifierHash,
  //   recipient: bigInt(recipient),
  //   relayer: bigInt(relayerAddress),
  //   fee: bigInt(fee),
  //   refund: bigInt(refund),

  //   // Private snark inputs
  //   nullifier: deposit.nullifier,
  //   secret: deposit.secret,
  //   pathElements: pathElements,
  //   pathIndices: pathIndices
  // }

  const input = {
      // Public snark inputs
      root: root,
      nullifierHash: toJson(deposit.nullifierHash),
      recipient: toJson(bigInt(recipient)),
      relayer: toJson(bigInt(relayerAddress)),
      fee: toJson(bigInt(fee)),
      refund: toJson(bigInt(refund)),
  
      // Private snark inputs
      nullifier: toJson(deposit.nullifier),
      secret: toJson(deposit.secret),
      pathElements: pathElements,
      pathIndices: pathIndices
    }

  // console.log('Generating SNARK proof');
  // console.time('Proof time');
  // console.log(relayerURL + '/v1/proofData');
  // console.log(input);
  
  const response = await axios.post(relayerURL + '/v1/proofData', input, {});
  const { proofData } = response.data;
  const { proof } = websnarkUtils.toSolidityInput(proofData);
  console.timeEnd('Proof time');

  const args = [
    toHex(input.root),
    toHex(input.nullifierHash),
    toHex(input.recipient, 20),
    toHex(input.relayer, 20),
    toHex(input.fee),
    toHex(input.refund)
  ];

  return { proof, args };
}