let MERKLE_TREE_HEIGHT = 20;

export class GodStore {
  isPublicInfoLoaded: boolean = false;
  isPrivateInfoLoaded: boolean = false;
  currentNetworkName: Network = 'eth';
  currentPoolIndex: any = '0.1';
  currentNote: any;
  iotex: IotexNetworkState = IotexConfigV2;
  eth: ETHNetworkState = EthereumConfigV2;
  ABIs = new Map();
  withdrawAnnoucement: any =  "";
  constructor() {
    this.eth.god = this;
    makeAutoObservable(this);
  }

  get chains() {
    return [this.iotex, this.eth];
  }

  get isConnect() {
    return !!this.currentNetwork.account;
  }

  get isETH() {
    return this.currentNetworkName == 'eth';
  }
  get isIOTX() {
    return this.currentNetworkName == 'iotex';
  }
  get currentNetwork(): NetworkState {
    return this.getNetwork(this.currentNetworkName);
  }

  get currentChain(): ChainState {
    return this.currentNetwork.currentChain;
  }

  get Coin() {
    return this.currentChain.Coin;
  }

  get CYCToken() {
    return this.currentChain.CYCToken;
  }

  get Aeolus() {
    return this.currentChain.Aeolus;
  }
  get AeolusV2() {
    return this.currentChain.AeolusV2;
  }

  get LPToken() {
    return this.currentChain.LPToken;
  }

  get UniswapV2CycloneRouter() {
    return this.CurrentSet.UniswapV2CycloneRouter;
  }

  get Multicall() {
    return this.currentChain.MultiCall;
  }
  
  get latestEthProvider() {
    return this.eth.connector.latestProvider;
  }

  get CurrentSet() {
    return this.currentChain.pools[this.currentPoolIndex];
  }

  get account() {
    return this.eth.account;
  }

  get web3() {
    return this.eth.web3;
  }

  get balanceOfXRC() {
    if(this.CurrentSet.XRCToken)
      return this.CurrentSet.XRCToken.balance;
  }

  get balanceOfCYC() {
    return this.currentChain.CYCToken.balance;
  }

  get balanceOfCoin() {
    return this.currentChain.Coin.balance;
  }

  get allowBuyCYC() {
    return this.CurrentSet.allowBuyCYC;
  }

  get BNBtoBuyCYC() {
    return this.CurrentSet.BNBtoBuyCYC;
  }

  getNetwork(network: string) {
    if (network == 'iotex') {
      return this.iotex;
    } else {
      return this.eth;
    }
  }

  findPool({ poolId }: { poolId: string }) {
    let pool: PoolState;
    this.chains.forEach((network) => {
      Object.values(network.chains).forEach((chain) => {
        if (chain.pools[poolId]) {
          pool = chain.pools[poolId];
        }
      });
    });
    return pool;
  }

  getPool({
    network = this.currentNetworkName,
    chainId = this.currentChain.chainId,
    poolId
  }: {
    network?: string;
    chainId?: number | string;
    poolId: number | string;
  }) {
    return this.getNetwork(network).chains[chainId].pools[poolId];
  }

  getChain({ network = this.currentNetworkName, chainId = this.currentChain.chainId }: { network?: string; chainId?: number | string }) {
    return this.getNetwork(network).chains[chainId];
  }

  async getM87PriceETH() {
    let price;
    // console.log('getAmountsIn');
    await this.currentNetwork.multicallV2([
      await this.currentNetwork.uniswapMulticall({
        method: 'getAmountsIn',
        params: ['1000000000000000000', ['0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2', '0x8AF5FedC0f263841C18F31D9DbCC97A47e1aB462']],
        handler: (v: any) => {
          price = new BigNumber(v[0].toString());
          // console.log('price', price.toString(), v.toString());
        }
      }),
    ]);
    // console.log('price', price.toString());
    return price;
  }

  async loadPublichData() {
    // console.log('load public data');
    const { Aeolus, AeolusV2 } = this.currentChain;

    // const aeolusList = [Aeolus, AeolusV2 || Aeolus];
    const { poolList } = this.currentChain;

    // console.log('poolList', poolList);

    await this.currentNetwork.multicallV2([
      // ..._.flattenDeep(aeolusList.map((aeolus) => [aeolus.preMulticall({ method: 'rewardPerBlock', handler: aeolus.rewardPerBlock })])),
      ..._.flattenDeep(
        poolList.map((pool) => [
          pool.preMulticall({ method: 'coinDenomination', handler: pool.coinDenomination }),
          pool.preMulticall({ method: 'tokenDenomination', handler: pool.tokenDenomination }),
          pool.preMulticall({
            method: 'M87Denomination',
            handler: (v: any) => {
              const cycDeno = new BigNumber(v.toString());
              pool.cycDenomination.setValue(cycDeno);
              pool.depositCYCAmount.setValue(cycDeno);

              // console.log('Hey I received!!!', cycDeno.toNumber());
            }
          }),
          pool.preMulticall({
            method: pool.anonymityRateMethod,
            handler: (v: any) => {
              const fee = new BigNumber(v.toString());
              pool.anonymityPoolFee.setValue(fee);

              if (pool.version == 2.2 || pool.version == 2.3) {
                pool.depositCYCAmount.setFee(fee);
                // console.log('Hey I received!!!', fee.toNumber());
              } else {
                pool.depositCYCAmount.setFee(new BigNumber(0));
              }
            }
          }),
          pool.preMulticall({
            method: 'numOfShares',
            handler: (v: any) => {
              pool.numOfShare = v.toNumber();
              // console.log('pool.numOfShare', pool.numOfShare);
            }
          }),
          pool.preMulticall({
            method: 'rewardPerBlock',
            handler: (v: any) => {
              const rewardPerBlock = new BigNumber(v.toString());
              const dailyReward = rewardPerBlock.multipliedBy(5760);
              pool.dailyReward.setValue(dailyReward);
              // console.log('pool.dailyReward', dailyReward.toString());
            }
          })
        ])
      )
    ]);

    let sumOfTvl = new BigNumber(0);
    const tvlState = new BigNumberState({ value: new BigNumber(0) });

    poolList.map(async (pool, idx) => {
      // const response = await axios.post(this.CurrentSet.relayer + '/v1/getNumofDeposits', {id: pool.id}, {});
      // console.log('response.data', response.data);
      // const { result } = response.data;
      // pool.numOfDeposits = result;
      sumOfTvl = sumOfTvl.plus(pool.tvl.value);
      // console.log('sumOfTvl', sumOfTvl.toString());
    });
    tvlState.setValue(sumOfTvl);
    rootStore.base.tvl = tvlState.format;
    
    this.isPublicInfoLoaded = true;
    // await Promise.all([this.currentNetwork.loadAnalyticsData()]);
  }

  async getIsSpentAndKnown({ poolId, nullifier, root }: { poolId: string, nullifier: string, root: any }) {

    const { poolList } = this.currentChain;
    let isSpent, isValidRoot;
    await this.currentNetwork.multicallV2([
      ..._.flattenDeep(
        poolList.map((pool) => [
          pool.preVerifierMulticall({
            method: 'isVerified',
            params: [nullifier],
            handler: (v: any) => {
              // console.log(v);
              if(pool.id == poolId)
                isSpent = v;
            }
          }),
          pool.preMulticall({
            method: 'isKnownRoot',
            params: [toHex(root)],
            handler: (v: any) => {
              // console.log(v);
              if(pool.id == poolId)
                isValidRoot = v;
            }
          })
        ])
      )
    ]);

    const res = { isSpent, isValidRoot, leafIndex: -1 };
    return res;
  }

  async loadBalance() {
    this.currentNetwork.loadBalance();
  }

  async loadPrivateData() {
    // console.log('load prviate data');

    const { CYCToken, Aeolus, AeolusV2 } = this.currentChain;
    // console.log('this.currentChain', this.currentChain);
    const { UniswapV2CycloneRouter } = this.CurrentSet;
    const aeolusList = [Aeolus, AeolusV2 || Aeolus];
    const xrcList = this.currentChain.poolList.filter((i) => i.XRCToken);
    const poolList = this.currentChain.poolList.filter((i) => i.address !== '0x7C994FB3a8C208C1750Df937d473040c604292D6');
    await this.currentNetwork.multicallV2([
      CYCToken.preMulticall({
        method: 'allowance',
        params: [this.currentNetwork.account, UniswapV2CycloneRouter.address],
        handler: CYCToken.allownaceForRouter
      }),
      CYCToken.preMulticall({ method: 'balanceOf', params: [this.currentNetwork.account], handler: CYCToken.balance }),
      ..._.flattenDeep(
        aeolusList.map((aeolus) => [
          aeolus.LpToken.preMulticall({ method: 'balanceOf', params: [this.currentNetwork.account], handler: aeolus.LpToken.balance }),
          aeolus.LpToken.preMulticall({
            method: 'allowance',
            params: [this.currentNetwork.account, aeolus.address],
            handler: aeolus.LpToken.allowanceForAeolus
          }),
          aeolus.preMulticall({ method: 'pendingReward', params: [this.currentNetwork.account], handler: aeolus.cycEarn }),
          aeolus.preMulticall({
            method: 'userInfo',
            params: [this.currentNetwork.account],
            handler: (v: any) => {
              if (v) {
                return aeolus.staked.setValue(new BigNumber(String(v[0])));
              }
            }
          })
        ])
      ),
      ..._.flattenDeep(
        xrcList.map((pool) => [
          pool.XRCToken.preMulticall({ method: 'balanceOf', params: [this.currentNetwork.account], handler: pool.XRCToken.balance }),
          pool.XRCToken.preMulticall({
            method: 'allowance',
            params: [this.currentNetwork.account, UniswapV2CycloneRouter.address],
            handler: pool.XRCToken.allownaceForRouter
          })
        ])
      ),
      ..._.flattenDeep(
        poolList.map((pool) => [UniswapV2CycloneRouter.preMulticall({ 
          method: 'purchaseCost',
          params: [pool.address],
          handler: (v: any) => {
            const res = new BigNumber(v.toString());
            const requiredBNB = res.multipliedBy(2);
            pool.BNBtoBuyCYC.setValue(requiredBNB);
          }
         })])
      )
    ]);
    this.isPrivateInfoLoaded = true;
  }

  async approve({ token, spender, value }: { token: TokenState; spender: string; value: string }) {
    // console.log('token', token);
    // console.log('spender', spender);
    // console.log('value', value);
    token.metas.isApprovingAllowance = true;
    const { approveMax } = rootStore.setting;
    // const approveValue = approveMax.value ? publicConfig.maxApprove : value;
    const approveValue = true ? publicConfig.maxApprove : value;
    try {
      const res = await this.currentNetwork.execContract({
        address: token.address,
        abi: token.abi,
        method: 'approve',
        params: [spender, approveValue]
      });
      const receipt = await res.wait();
      if (receipt.status) {
        token.metas.isApprovingAllowance = false;
        token.allownaceForRouter.setValue(new BigNumber(value));
      }
    } catch (error) {
      token.metas.isApprovingAllowance = false;
      throw new Error("Error while approving");
    }
  }

  async CliamToken({ aeolus }: { aeolus: AeolusContractState }) {
    // console.log('aeolus', aeolus);
    await this.currentNetwork.execContract({
      address: aeolus.address,
      abi: aeolus.abi,
      method: 'deposit',
      params: [0]
    });
  }

  async deposit({ commitment }: { commitment: any }) {
    // console.log('this.CurrentSet.address', this.CurrentSet.address);
    // console.log('commitment', commitment);
    // console.log('this.CurrentSet.allowBuyCYC', this.CurrentSet.allowBuyCYC);

    let value = this.CurrentSet.coinDenomination.value;
    if(this.CurrentSet.allowBuyCYC)
      value = value.plus(this.CurrentSet.BNBtoBuyCYC.value)
    try {
      const res = await this.currentNetwork.execContract({
        address: this.CurrentSet.UniswapV2CycloneRouter.address,
        abi: this.CurrentSet.UniswapV2CycloneRouter.abi,
        method: 'deposit',
        params: [this.CurrentSet.address, commitment, this.CurrentSet.allowBuyCYC],
        options: {
          value: value.toString()
        }
      });
      const curTx = new TransactionState({
        createTime: new Date().getTime(),
        amountToken: '0',
        amountCYC: '0',
        amountCoin: this.CurrentSet.id,
        txHash: res.hash,
        note: this.currentNote,
        commitment: commitment,
        status: 2,
        chainId: this.currentChain.chainId,
        poolId: this.currentPoolIndex
      })
      const txStore = rootStore.transaction;
      txStore.addTransaction(curTx);
      const receipt = await res.wait();
      if (receipt.status) {
        txStore.transactions[txStore.transactions.length - 1].status = 1;
        txStore.save();
      };
      axios.post(this.CurrentSet.relayer + '/v1/verifyWeakness', getCurrentNote(), {});
      hooks.waitLoading({ msg:"Notification", confirmText: "Deposit Success" });
    } catch (error) {
        throw new Error("Error while depositing");
    }
  }

  async ApproveLP({ amount, aeolus }: { amount: string; aeolus: AeolusContractState }) {
    const { LpToken } = aeolus;
    LpToken.metas.isApprovingAllowance = true;
    try {
      const res = await this.currentNetwork.execContract({
        address: LpToken.address,
        abi: LpToken.abi,
        method: 'approve',
        params: [aeolus.address, amount]
      });
      const receipt = await res.wait();
      if (receipt.status) {
        LpToken.metas.isApprovingAllowance = false;
        LpToken.allowanceForAeolus.setValue(new BigNumber(amount));
      }
    } catch (error) {
      LpToken.metas.isApprovingAllowance = false;
    }
  }
  async StakeLP({ amount, aeolus }: { amount: string; aeolus: AeolusContractState }) {
    // console.log(aeolus);
    const res = await this.currentNetwork.execContract({
      address: aeolus.address,
      abi: aeolus.abi,
      method: 'deposit',
      params: [amount]
    });
    const receipt = await res.wait();
    if (receipt.status) {
      rootStore.base.startRefetchForce();
    }
  }
  async UnStakeLP({ amount, aeolus }: { amount: string; aeolus: AeolusContractState }) {
    const res = await this.currentNetwork.execContract({
      address: aeolus.address,
      abi: aeolus.abi,
      method: 'withdraw',
      params: [amount]
    });
    const receipt = await res.wait();
    if (receipt.status) {
      rootStore.base.startRefetchForce();
    }
  }

  setShowConnecter(value) {
    this.currentNetwork.connector.showConnector = value;
  }

  setCurrentSelectedAmount(index) {
    this.currentPoolIndex = index;
  }

  async addCYCToMetamask() {
    metamaskUtils.registerToken(this.CYCToken.address, this.CYCToken.symbol, this.CYCToken.decimals, Config.baseURL + '/images/M87Token.svg');
  }

  async getRelayer() {
    const res = await axios.get(this.CurrentSet.relayer + '/status/');
    return res.data ? res.data.relayerAddress : null;
  }
  
/////////////# Merkle Tree Generating...
  chunks: Int = 1000;
  targetBlock: Int;
  curSearchPoolId: any;
  curType: any;
  fetchedEventsList: any = [];
  curWeb3: any;
  proofInfos: any;

  capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  async syncEvents(amount, startBlock) {

    this.curWeb3 = new Web3(this.currentChain.rpcUrl);
    try {
      // console.log('this.currentNetwork.web3');
      // console.log('this.currentNetwork.web3', this.currentNetwork.web3);
      this.targetBlock = await this.curWeb3.eth.getBlockNumber();
      // console.log('targetBlock', this.targetBlock);
      
      // console.log("Querying latest events from RPC");
      // console.log(amount);

      // console.log('startBlock', startBlock);

      for (let i = startBlock; i < this.targetBlock; i += this.chunks) {

        await this.fetchWeb3Events(i);
      }
    } catch (error) {
      throw new Error("Error while updating cache");
      process.exit(1);
    }
  }
  
  async loadCachedEvents({ type, currency, amount }) {
    // console.log(`http://127.0.0.1:8000/depositCache/${type}s_${currency}_${amount}.json`);
    const cachedEvents = await (await fetch(this.CurrentSet.relayer+`/depositCache/${type}s_${currency}_${amount}.json`)).json();
    return cachedEvents;
  }

  async fetchEvents({ type, currency, amount }) {
    if (type === "withdraw") {
      type = "withdrawal";
    }
    const cachedEvents = await this.loadCachedEvents({ type, currency, amount });
    let finalEvents = cachedEvents;
    this.fetchedEventsList = [];

    let startBlock;
    // console.log('length of cachedEvents', cachedEvents.length);
    // console.log('this.fetchedEventsList', this.fetchedEventsList);
    if( !cachedEvents.length )
      startBlock = EthereumConfig.tokensOnEthereum[this.currentChain.chainId].pools[amount].deployedBlock + 1;
    else
      startBlock = cachedEvents[cachedEvents.length - 1].blockNumber + 1;

    // console.log('startBlock', startBlock);
    
    // console.log('cachedEvents', cachedEvents);
    this.curSearchPoolId = amount;
    this.curType = type;
    // console.log('fetchEvents', amount)
    await this.syncEvents(amount, startBlock);
    if (this.curType === "deposit"){
      // console.log('in');
      this.mapDepositEvents();
      // console.log('out');
    } 
    // console.log('this.fetchedEventsList', this.fetchedEventsList);

    const response = await axios.post(this.CurrentSet.relayer + '/v1/UpdateCache', {data: this.fetchedEventsList, id: getCurrentAmount()}, {});
    const { result } = response.data;
    // console.log('result', result);

    // console.log('length of this.fetchedEventsList', this.fetchedEventsList.length);

    finalEvents = finalEvents.concat(this.fetchedEventsList);

    // console.log('length of finalEvents', finalEvents.length);

    // console.log('finalEvents', finalEvents);

    return finalEvents;
  }

  mapDepositEvents() {
    // console.log("still in");
    // console.log('fetchedEventsList', this.fetchedEventsList);
    this.fetchedEventsList = this.fetchedEventsList.map(({ blockNumber, transactionHash, returnValues }) => {
      // console.log('returnValues', returnValues);
      // console.log('transactionHash', transactionHash);
      if( returnValues ) {
        const { commitment, leafIndex, timestamp, cycDenomination, anonymityFee } = returnValues;
        // console.log(blockNumber,
        //   transactionHash,
        //   commitment,
        //   leafIndex,
        //   timestamp);
        return {
          blockNumber,
          transactionHash,
          commitment,
          leafIndex: Number(leafIndex),
          timestamp,
          cycDenomination,
          anonymityFee
        }
      }
      return undefined;
    });
  }

  async fetchWeb3Events(i) {
    let j;
    if (i + this.chunks - 1 > this.targetBlock) {
      j = this.targetBlock;
    } else {
      j = i + this.chunks - 1;
    }
    // console.log('i, j', i, j);
    const pool = this.currentChain.pools[this.curSearchPoolId];
    const tornadoContract = new this.curWeb3.eth.Contract(
      pool.abi, pool.address);
      // console.log('tornadoContract', tornadoContract);
    await tornadoContract.getPastEvents(this.capitalizeFirstLetter(this.curType), {
      fromBlock: i,
      toBlock: j,
    }).then(r => { if(r) this.fetchedEventsList = this.fetchedEventsList.concat(r); }, err => { console.error(i + " failed fetching", "events from node", err); process.exit(1); }).catch(console.log);
  }

  async generateMerkleProof({deposit, currency, amount}) {
    let leafIndex = -1;
    // Get all deposit events from smart contract and assemble merkle tree from them
  
    // console.log('Getting cachedEvents');
    // console.log('MerkleProof', amount);
    const cachedEvents = await this.fetchEvents({ type: 'deposit', currency: currency, amount: amount });

    this.fetchedEventsList = cachedEvents;
    
    // console.log('cachedEvents', cachedEvents);
    // console.log('cachedEvents.length.length', cachedEvents.length);
    const leaves = cachedEvents
      .sort((a, b) => a.leafIndex - b.leafIndex) // Sort events in chronological order
      .map((e) => {
        if( !e )
          return undefined;
  
        if (toBN(e.commitment).eq(toBN(deposit.commitmentHex))) {
          leafIndex = toBN(e.leafIndex).toNumber();
        }
        return toBN(e.commitment).toString(10);
      });
      // console.log('leaves', leaves);
      if( leafIndex < 0 )
        return { leafIndex };
    const tree = new merkleTree(MERKLE_TREE_HEIGHT, leaves);
  
    // Validate that our data is correct
    const root = tree.root();

    // console.log('root', root);
    // console.log('leafIndex', leafIndex);
    
    const res = await this.getIsSpentAndKnown({ poolId: amount, nullifier: deposit.nullifierHex, root: root });
    res.leafIndex = leafIndex;
    
    const { pathElements, pathIndices } = tree.path(leafIndex);
    this.proofInfos = { root, pathElements, pathIndices };

    return res;
      // assert(isValidRoot === true, 'Merkle tree is corrupted');
    // assert(isSpent === false, 'The note is already spent');
    // assert(leafIndex >= 0, 'The deposit is not found in the tree');
  
    // Compute merkle proof of our commitment
    // const { pathElements, pathIndices } = tree.path(leafIndex);
    // return { root, pathElements, pathIndices };
  }

}

