import { toHex, generateProof } from '../../../lib/zSnarksWorker';
import axios from 'axios';
import { toBN, BN } from 'web3-utils';

let currentStatusId;

function fromDecimals({ amount, decimals }) {
    amount = amount.toString();
    let ether = amount.toString();
    const base = new BN('10').pow(new BN(decimals));
    const baseLength = base.toString(10).length - 1 || 1;
  
    const negative = ether.substring(0, 1) === '-';
    if (negative) {
      ether = ether.substring(1);
    }
  
    if (ether === '.') {
      throw new Error('[ethjs-unit] while converting number ' + amount + ' to wei, invalid value');
    }
  
    // Split it into a whole and fractional part
    const comps = ether.split('.');
    if (comps.length > 2) {
      throw new Error('[ethjs-unit] while converting number ' + amount + ' to wei,  too many decimal points');
    }
  
    let whole = comps[0];
    let fraction = comps[1];
  
    if (!whole) {
      whole = '0';
    }
    if (!fraction) {
      fraction = '0';
    }
    if (fraction.length > baseLength) {
      throw new Error('[ethjs-unit] while converting number ' + amount + ' to wei, too many decimal places');
    }
  
    while (fraction.length < baseLength) {
      fraction += '0';
    }
  
    whole = new BN(whole);
    fraction = new BN(fraction);
    let wei = whole.mul(base).add(fraction);
  
    if (negative) {
      wei = wei.mul(negative);
    }
  
    return new BN(wei.toString(10), 10);
}

function calculateFee({ currency, gasPrice, amount, refund, ethPrices, relayerServiceFee, decimals }) {
    const decimalsPoint =
      Math.floor(relayerServiceFee) === Number(relayerServiceFee) ? 0 : relayerServiceFee.toString().split('.')[1].length;
    const roundDecimal = 10 ** decimalsPoint;
    const total = toBN(fromDecimals({ amount, decimals }));
    const feePercent = total.mul(toBN(relayerServiceFee * roundDecimal)).div(toBN(roundDecimal * 100));
    const expense = toBN(gasPrice).mul(toBN(3e6));
    let desiredFee;
    
    desiredFee = expense.add(feePercent);

    // console.log('expense', expense.toString());
    // console.log('desiredFee', desiredFee.toString());
    return desiredFee;
}


  
export const Withdraw = observer((props) => {
  const { lang, god, dev, base } = useStore();

  function getStatus(id, relayerURL, options) {
    return new Promise((resolve) => {
      async function getRelayerStatus() {
        if( currentStatusId != id )
          return;
        const responseStatus = await axios.get(relayerURL + '/v1/jobs/' + id, options);
  
        if (responseStatus.status === 200) {
          const { txHash, status, confirmations, failedReason } = responseStatus.data
          
          god.withdrawAnnoucement = `Transaction status ${status}, confirmations: ${confirmations ? confirmations: '0'}`;
          
          // console.log(`Current job status ${status}, confirmations: ${confirmations}`);
  
          if (status === 'FAILED') {

            god.withdrawAnnoucement = "";
            throw new Error(status + ' failed reason:' + failedReason);
          }
  
          if (status === 'CONFIRMED') {
            const receipt = await waitForTxReceipt({ txHash });
            // console.log(
            //   `Transaction submitted through the relay. View transaction on block explorer ${txHash}`
            // );
            // console.log('Transaction mined in block', receipt.blockNumber);
            // resolve(status);
          }
        }
  
        setTimeout(() => {
          getRelayerStatus(id, relayerURL);
        }, 3000)
      }
  
      getRelayerStatus();
    })
  }

function waitForTxReceipt({ txHash, attempts = 60, delay = 1000 }) {
    return new Promise((resolve, reject) => {
      const checkForTx = async (txHash, retryAttempt = 0) => {
        const result = await god.curWeb3.eth.getTransactionReceipt(txHash);
        if (!result || !result.blockNumber) {
          if (retryAttempt <= attempts) {
            setTimeout(() => checkForTx(txHash, retryAttempt + 1), delay);
          } else {
            reject(new Error('tx was not mined'));
          }
        } else {
          resolve(result);
        }
      }
      checkForTx(txHash);
    })
}

  return (
    <div style={{ display: props.isChosen? 'block' : 'none' }}>
    <Wrapper>
        <WithdrawViewForETH
            withdrawStart = {async (deposit, currency, amount, recipient, relayerURL, refund = 0, fee ) => {
              // console.log(deposit, currency, amount, recipient, relayerURL, refund);
  
                god.withdrawAnnoucement = "Generating Proof...";
                currentStatusId = undefined;

                const relayerStatus = await axios.get(relayerURL + '/status');
                const { rewardAccount, netId, ethPrices, tornadoServiceFee } = relayerStatus.data;
              //   if( netId != god.currentChain.chainId )
              //       return;
              //   console.log('Relay address:', rewardAccount);
              //   const gasPrice = await fetchGasPrice(netId);
              //   const decimals = 18;
              //   const fee = calculateFee({
              //       currency,
              //       gasPrice,
              //       amount,
              //       refund,
              //       ethPrices,
              //       relayerServiceFee: tornadoServiceFee,
              //       decimals
              //   });
                
                // console.log("start generateProof");
                const { proof, args } = await generateProof({ deposit, currency, amount, recipient, relayerAddress: rewardAccount, fee, refund, poolInfos: god.proofInfos, relayerURL });
                // console.log('args', args);
                // console.log('Sending withdraw transaction through relay');
                god.withdrawAnnoucement = "Sending withdraw transaction through relayer";
                try {
                    const response = await axios.post(relayerURL + '/v1/tornadoWithdraw', {
                      contract: god.currentChain.pools[amount].address,
                      proof,
                      args
                    }, {});
              
                    const { id } = response.data;
                    currentStatusId = id;
              
                    const result = await getStatus(id, relayerURL, {});
                    
                    // console.log('STATUS', result);
                } catch (e) {
                    if (e.response) {
                      console.error(e.response.data.error);
                    } else {
                      console.error(e.message);
                    }
                    god.withdrawAnnoucement = "";
                }
                // console.log('Done withdrawal from Tornado Cash');
            }}
        />
    </Wrapper>
    </div>
  );
});
