		try {
			const options = {
			chainId: 1,
			defaultRpc: 'https://nameless-spring-field.quiknode.pro/3313199c6778abecbc80fc7f7066931bbb743113/',
			timeout: 10000,
			defaultFallbackGasPrices: {
			  instant: 28,
			  fast: 22,
			  standard: 17,
			  low: 11,
			},
		  };
		  // Bump fees for Ethereum network
		  if (netId == 1) {
			const oracle = new GasPriceOracle(options);
			const fallbackGasPrices = {
				instant: 70,
				fast: 31,
				standard: 20,
				low: 7,
			};
			const gas = await oracle.gasPrices(fallbackGasPrices);
			// console.log('gasPricesETH(gas.instant)', gasPricesETH(gas.instant));
			return gasPricesETH(gas.instant);
		  } else if ( netId == 3 ) {
			const web3GasPrice = await god.curWeb3.eth.getGasPrice();
			// console.log('web3GasPrice', web3GasPrice);
			return "0x0";
		  }
		} catch (err) {
		  throw new Error(`Method fetchGasPrice has error ${err.message}`);
		}
