import { observer, useLocalStore } from 'mobx-react-lite';
import React, { useEffect } from 'react';
import { useStore } from '../store';
import Button from './Button';

export const Loading = observer((props) => {
  const { lang } = useStore();

  const store = useLocalStore(
    (props) => ({
      allowToCancel: false,
      nextable: true,
      next() {
        props.next();
        store.nextable = false;
      },
    }),
    props
  );

  useEffect(() => {
    if (props.action) {
      props.action();
    }

    setTimeout(() => {
      store.allowToCancel = true;
    }, 7000);
  }, []);

  return (
    <div className="loadingBar">
      <svg style={{ margin: 'auto', background: 'rgb(255, 255, 255, 0)', display: 'block', shapeRendering: 'auto', width: 200, height: 200 }} viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
      <circle cx="50" cy="50" r="0" fill="none" stroke="#44f1a6" stroke-width="2">
        <animate attributeName="r" repeatCount="indefinite" dur="1s" values="0;39" keyTimes="0;1" keySplines="0 0.2 0.8 1" calcMode="spline" begin="0s"></animate>
        <animate attributeName="opacity" repeatCount="indefinite" dur="1s" values="1;0" keyTimes="0;1" keySplines="0.2 0 0.8 1" calcMode="spline" begin="0s"></animate>
      </circle><circle cx="50" cy="50" r="0" fill="none" stroke="#44f1a6" stroke-width="2">
        <animate attributeName="r" repeatCount="indefinite" dur="1s" values="0;39" keyTimes="0;1" keySplines="0 0.2 0.8 1" calcMode="spline" begin="-0.5s"></animate>
        <animate attributeName="opacity" repeatCount="indefinite" dur="1s" values="1;0" keyTimes="0;1" keySplines="0.2 0 0.8 1" calcMode="spline" begin="-0.5s"></animate>
      </circle>
      </svg>

      <div className="loadingLabel">{props.text}</div>

      <div
        style={{
          display: 'flex',
          flexDirection: 'column'
        }}
      >
        {props.nextable && (
          <Button
            fullWidth={false}
            avaliable={props.nextable && store.nextable}
            onClick={store.next}
            label={props.positiveButtonLabel ? props.positiveButtonLabel : lang.t('Confirm')}
          />
        )}

        {props.cancelable && (
          <div style={{ marginTop: "1rem" }}>
            <Button fullWidth={false} avaliable={store.allowToCancel} onClick={props.cancel} label={lang.t('Cancel')} />
          </div>
        )}
      </div>
    </div>
  );
});

export default Loading;
