import React, { useEffect, useMemo } from 'react';
import Button from './Button';
import { Popover } from 'antd';
import { css } from '../modules/index';
import { useStore } from '../store';
import { observer, useLocalStore, useObserver } from 'mobx-react-lite';
import ReactTooltip from 'react-tooltip';
import BigNumber from 'bignumber.js';
import EthereumConfig from '../EthereumConfig';
import { toast } from 'react-toastify';
import { debounce } from 'lodash';
import { eventBus } from '../lib/event';
import { SendActionRequest } from 'iotex-antenna/lib/rpc-method/types';
import CountUp from 'react-countup';
import { useWeb3React } from '@web3-react/core';
import { BigNumberState } from '../store/type';
import { Icon, Text, FlexBox, Wrapper, BorderBox, DropBox, Section, Input } from '../modules/globalStyle';
import { SingleSymbol } from './SingleSymbol';
import LoadingButton from '@mui/lab/LoadingButton';
import { TransactionState } from '../store/lib/TransactionState';
import { GodStore } from '../store/god';

let clipboardy = null;

export const DepositViewForBSC = observer(props => {
  const { lang, god: pageStore, base: baseStore } = useStore();
  const { activate, error, deactivate } = useWeb3React();
  const store = useLocalStore(() => ({
    tokens: null,
    // indexOfToken: 0,
    // indexOfAmountSet: 0,
    isShowDetail: false,
    indexOfPool: null,
    currentAmount: new BigNumberState({ loading: true }),
    currentCYCDenomination: new BigNumberState({ loading: true }),
    currentCoinDenomination: new BigNumberState({ loading: true }),
    poolSize: -1,
    anonymityFee: new BigNumberState({ loading: true }),
    ApproveStatus: "disabled",
    DepositStatus: "disabled",
    WithdrawStatus: "disabled",

    getAnonymityFee() {
      if (!store.indexOfPool) {
        return;
      }
      this.anonymityFee.setValue(pageStore.CurrentSet.anonymityPoolFee.value);
    },

    // get currentCYCDenominationLabel() {
    //   return '...';
    // },

    get xrcApproved() {
      return new BigNumber(pageStore.XRCAllowance).comparedTo(store.currentAmount.value) >= 0;
    },

    get cycApproved() {
      return (
        new BigNumber(pageStore.currentChain.CYCToken.allownaceForRouter.value).comparedTo(store.currentCYCDenomination.value) >= 0 &&
        store.currentCYCDenomination.value.comparedTo('0') > 0
      );
    },

    init(chainID) {
      // console.log('EthereumConfig.tokensOnEthereum[chainID] =', EthereumConfig.tokensOnEthereum[chainID]);

      store.tokens = pageStore.currentChain.pools;
      // console.log('store.tokens =', store.tokens);
      // console.log('cyc denomination', store.currentCYCDenomination.format);
      // console.log(store.tokens.length);
      if( store.tokens ) {
        // console.log('ininin');
        this.indexOfPool = props.indexOfPool;
      }
      // console.log('this.indexOfPool =', this.indexOfPool);
      this.loadAmount();
    },

    loadAmount() {
      if (pageStore.web3) {
        store.getCurrentAmount();
        store.getPoolSize();
        store.getAnonymityFee();
        store.getCurrentCYCDenomination();
        store.getCurrentCoinDenomination();
      }
      if (pageStore.account) {
        // pageStore.getBalanceOfXRC();
        // pageStore.getBNBtoBuyCYC();
        // pageStore.getBalanceOfCoin();
      }
    },

    // onSelectAmount(id) {
    //   pageStore.setCurrentSelectedAmount(id);
    // },

    // onSelectToken(event) {
    //   const idx = event.target.value;
    //   store.indexOfToken = idx;
    //   props.onSelectToken(idx);
    // },

    onSelectPool() {
      pageStore.setCurrentSelectedAmount(store.indexOfPool);
      this.loadAmount();
    },

    onConnect() {
      if (props.onConnect) {
        props.onConnect();
      }
    },

    logout() {
      deactivate();
      pageStore.latestEthProvider.clear();
    },

    // onSelectPool(event) {
    //   const tempInt = event.target.value;
    //   store.indexOfPool = tempInt;
    //   props.onSelectAmount(tempInt);
    // },

    async onApproveXRC() {
      props.onApproveXRC(pageStore.CurrentSet.XRCToken.address, store.currentAmount.value);
    },

    async onApproveCYC() {
      store.ApproveStatus = "loading";
      try {
        await props.onApproveCYC(pageStore.currentChain.CYCToken, store.currentCYCDenomination.value.toString());
        store.ApproveStatus = "disabled";
      } catch (error) {
        store.ApproveStatus = "notApproved";
      }
    },

    async deposit() {
      store.DepositStatus = "loading";
      // let set = store.tokens[store.indexOfToken].amountSteps[store.indexOfPool];
      let set = pageStore.CurrentSet;

      if (store.currentAmount.value.comparedTo(0) <= 0) {
        //### const abi = await God.asyncGetABI(set.abi);
        // store.currentAmount.setValue(new BigNumber(await God.asyncGetDepositDenomination(set.address, abi)));
      }

      if (set.address) {
        try {
          await props.onDeposit();
          store.DepositStatus = store.allowDeposit;
        } catch (error) {
          store.DepositStatus = "active";
        }
        // console.log('alreadySent', 'alreadySent');
        //### props.onDeposit(
        //   set.address,
        //   store.currentAmount.value,
        //   store.currentCYCDenomination.value,
        //   pageStore.balanceOfCYC.value,
        //   store.currentCoinDenomination.value,
        //   set.id,
        //   pageStore.allowBuyCYC
        // );
      }
    },

    async getCurrentAmount() {
      if (!store.indexOfPool) {
        return;
      }

      //const set = store.tokens[store.indexOfToken].amountSteps[store.indexOfPool];
      const value = pageStore.CurrentSet.tokenDenomination.value;
      store.currentAmount.setValue(value);
      store.currentAmount.setLoading(false);
    },

    async getCurrentCoinDenomination() {
      if (!store.indexOfPool) {
        return;
      }

      const value = pageStore.CurrentSet.coinDenomination.value;
      store.currentCoinDenomination.setValue(value);
      store.currentCoinDenomination.setLoading(false);
    },

    async getCurrentCYCDenomination() {
      if (!store.indexOfPool) {
        return;
      }

      // const set = store.tokens[store.indexOfToken].amountSteps[store.indexOfPool];
      const value = pageStore.CurrentSet.depositCYCAmount.value;
      store.currentCYCDenomination.setValue(value);
      store.currentCYCDenomination.setLoading(false);
    },

    async getPoolSize() {
      if (!store.indexOfPool) {
        return;
      }

      store.poolSize = -1;
      if (store.tokens) {
        store.poolSize = pageStore.CurrentSet.numOfShare;
      }
    },

    onClickCopyButton(event) {
      if (clipboardy) {
        clipboardy.focus();
        clipboardy.select();
        const result = document.execCommand('copy');
        if (result === 'unsuccessful') {
          clipboardy.blur();
        } else {
          clipboardy.blur();
          toast.dark(lang.t('address.copied'), {
            position: 'bottom-center',
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: false,
            pauseOnHover: false,
            draggable: false,
            progress: undefined
          });
        }
      }
    },

    onChangeBuyCYCCheckBox(event) {
      if (!store.indexOfPool) {
        return;
      }
      
      pageStore.CurrentSet.allowBuyCYC = event.target.checked && !event.target.disabled;
    },

    get allowDeposit() {
      const { currentAmount, currentCYCDenomination, currentCoinDenomination } = store;
      const { balanceOfXRC, balanceOfCYC, balanceOfCoin, allowBuyCYC, BNBtoBuyCYC } = pageStore;
      // insufficient asset balance
      // console.log(currentAmount.value, "currentAmount.value");
      if (currentAmount.value.comparedTo(0) > 0 && (!this.xrcApproved || balanceOfXRC.value.comparedTo(currentAmount.value) < 0)) {
        return false;
      }
      // insufficient coin balance
      if (balanceOfCoin.value.comparedTo(currentCoinDenomination.value) < 0) {
        return false;
      }
      // insufficient cyc balance
      if (currentCYCDenomination.value.comparedTo(0) > 0) {
        if (allowBuyCYC && balanceOfCoin.value.comparedTo(currentCoinDenomination.value.plus(BNBtoBuyCYC.value)) < 0) {
          return false;
        }
        if (!allowBuyCYC && (!this.cycApproved || balanceOfCYC.value.comparedTo(currentCYCDenomination.value) < 0)) {
          return false;
        }
      }
      return true;
    }
  }));

  const ActionButton = useObserver(() => {
    if (!props.isAvaliable) {
      return store.indexOfPool && <Button avaliable={true} label={lang.t('connect')} onClick={store.onConnect} />;
    }
    return store.indexOfPool && <Button label={lang.t('Deposit')} avaliable={store.allowDeposit} onClick={store.deposit} />;
  });

  const AnonymityPool = useObserver(() => {
    if(store.isShowDetail)
    {
      if (!pageStore.isConnect) {
        return (
          <DropBox color="primary">
            <FlexBox justify="center" items="center">
              <div style={{color: 'white'}}>
                {lang.t('connect')}
              </div>
            </FlexBox>
          </DropBox>
        );
      }
      return (
        <div>
        <DropBox color="primary">
          <FlexBox justify="between" responsive="rc">
            <Section css={{ width: '100%', mb: '1.5rem', '@md': { width: '46%', mb: 0 } }}>
              <div
                style={{
                  marginTop: '1rem'
                }}
              >
                {store.currentAmount.value.comparedTo(0) <= 0 && (
                  <BorderBox css={{ padding: '0.6rem 0'}}>
                    <div style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '15px',
                          color: '#959191',
                          paddingBottom: '12px'
                    }}>
                      {lang.t('balance')}:&nbsp;
                      {pageStore.balanceOfCoin.getFormat({ decimals: pageStore.currentChain.Coin.decimals })} ETH &nbsp;
                      {store.currentCoinDenomination.value.comparedTo(pageStore.balanceOfCoin.value) > 0 && (
                        <>
                          <Popover
                            placement="top"
                            trigger="click"
                            content={
                              <>
                                <div style={{ textAlign: 'center', fontWeight: 'bold', maxWidth: '12rem' }}>
                                  {lang.t('amount.invalid')}
                                </div>
                                <div
                                  style={{
                                    textAlign: 'center',
                                    fontWeight: 'bold',
                                    maxWidth: '12rem',
                                    pointerEvents: 'auto'
                                  }}
                                ></div>
                              </>
                            }
                            backgroundColor="#45bcb8"
                          >
                            <img src="/images/alert.png" style={{ cursor: 'pointer' }} alt="" />
                          </Popover>
                        </>
                      )}
                    </div>

                    <div
                      className={styles.denomenation.className}
                      style={{
                        marginBottom: '0.5rem'
                      }}
                    >
                      <div
                        style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '18px',
                          color: 'white'
                        }}
                      >
                        ETH &nbsp;{lang.t('Denomination')}:&nbsp;
                        {store.currentCoinDenomination.getFormat({
                        decimals: pageStore.currentChain.Coin.decimals,
                        fixed: 8
                      })}
                      </div>
                    </div>
                  </BorderBox>
                )}

                {pageStore.CurrentSet.XRCToken && store.currentAmount.value.comparedTo(0) > 0 && (
                  <BorderBox css={{ padding: '0.6rem 0'}}>
                    <div style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '15px',
                          color: '#959191',
                          paddingBottom: '12px'
                    }}>
                      {lang.t('balance')}:&nbsp;{pageStore.balanceOfXRC.format}&nbsp;{pageStore.CurrentSet.XRCToken.symbol}
                      {store.currentAmount.value.comparedTo(pageStore.balanceOfXRC.value) > 0 && (
                        <>
                          <Popover
                            placement="top"
                            trigger="click"
                            content={
                              <>
                                <div style={{ textAlign: 'center', fontWeight: 'bold', maxWidth: '12rem' }}>
                                  {lang.t('amount.invalid')}
                                </div>
                                <div
                                  style={{
                                    textAlign: 'center',
                                    fontWeight: 'bold',
                                    maxWidth: '12rem',
                                    pointerEvents: 'auto'
                                  }}
                                ></div>
                              </>
                            }
                            backgroundColor="#45bcb8"
                          >
                            <img src="/images/alert.png" style={{ cursor: 'pointer' }} alt="" />
                          </Popover>
                        </>
                      )}
                    </div>

                    <div
                      className={styles.denomenation.className}
                      style={{
                        marginBottom: '0.5rem'
                      }}
                    >
                      <div
                        style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '18px',
                          color: 'white'
                        }}
                      >
                        {pageStore.CurrentSet.XRCToken.symbol}&nbsp;{lang.t('Denomination')}:&nbsp;
                        {store.currentAmount.format}
                      </div>

                      <Button
                        avaliable={pageStore.isConnect && !store.xrcApproved}
                        label={lang.t('approve') + ' ' + pageStore.CurrentSet.XRCToken.symbol}
                        disabledLabel={lang.t('approved')}
                        disable={store.xrcApproved}
                        onClick={store.onApproveXRC}
                        fontSize="12px"
                      />
                    </div>
                  </BorderBox>
                )}

                {store.currentCYCDenomination.value.comparedTo(0) > 0 && pageStore.account && (
                  <BorderBox css={{ padding: '0.6rem 0'}}>
                    <div style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '15px',
                          color: '#959191',
                          paddingBottom: '12px'
                    }}>
                      {lang.t('balance')}:&nbsp;
                      {pageStore.balanceOfCYC.format}
                      &nbsp;M87&nbsp;
                      {store.currentCYCDenomination.value.comparedTo(pageStore.balanceOfCYC.value) > 0 ? (
                        <>
                          <Popover
                            placement="top"
                            trigger="click"
                            content={
                              <>
                                <div style={{ textAlign: 'center', fontWeight: 'bold', maxWidth: '12rem' }}>
                                  {lang.t('cyc.tips')}
                                </div>
                                <div
                                  style={{
                                    textAlign: 'center',
                                    fontWeight: 'bold',
                                    maxWidth: '12rem',
                                    pointerEvents: 'auto'
                                  }}
                                >
                                  <a
                                    style={{
                                      color: 'black',
                                      textDecoration: 'underline'
                                    }}
                                    href={pageStore.currentChain.swapURL}
                                  >
                                    {lang.t('click.buy')}
                                  </a>
                                </div>
                              </>
                            }
                            backgroundColor="#45bcb8"
                          >
                            <img src="/images/alert.png" style={{ cursor: 'pointer' }} alt="" />
                          </Popover>
                        </>
                      ) : null}
                    </div>

                    <div className={styles.denomenation.className}>
                      <div
                        style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '18px',
                          color: 'white'
                        }}
                      >
                        {lang.t('cyc.denomenation')}:&nbsp;{store.currentCYCDenomination.format}
                        <Popover
                            placement="top"
                            trigger="click"
                            content={
                              <>
                                <div style={{ textAlign: 'center', fontWeight: 'bold', maxWidth: '12rem' }}>
                                  {lang.t('cyc.reward')}
                                </div>
                              </>
                            }
                            backgroundColor="#45bcb8"
                          >
                            <img src="/images/reward.png" style={{ cursor: 'pointer', paddingLeft: 5, marginTop: -2 }} alt="" />
                        </Popover>
                      </div>

                      
                    </div>

                    {pageStore.balanceOfCYC.value.comparedTo(store.currentCYCDenomination.value) < 0 && pageStore.account && (
                      <div
                        className="flex item-center"
                        style={{
                          marginTop: '0.5rem'
                        }}
                      >
                        <input
                          disabled={
                            pageStore.balanceOfCoin.value.comparedTo(
                              store.currentCoinDenomination.value.plus(pageStore.BNBtoBuyCYC.value)
                            ) < 0
                          }
                          id="check"
                          type="checkbox"
                          style={{ verticalAlign: 'middle' }}
                          onChange={store.onChangeBuyCYCCheckBox}
                        />
                        <label htmlFor="check" style={{ marginLeft: 10, verticalAlign: 'middle', color: 'white' }}>
                          {lang.t('use.coin.to.buy.cyc', {
                            amount: pageStore.BNBtoBuyCYC.format,
                            symbol: pageStore.currentChain.Coin.symbol
                          })}
                        </label>
                      </div>
                    )}
                  </BorderBox>
                )}

                {store.currentAmount.value.comparedTo(0) > 0 && (
                  <BorderBox css={{ padding: '0.6rem 0'}}>
                    <div style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '15px',
                          color: '#959191',
                          paddingBottom: '12px'
                    }}>
                      {lang.t('balance')}:&nbsp;
                      {pageStore.balanceOfCoin.getFormat({ decimals: pageStore.currentChain.Coin.decimals })} ETH
                      {store.currentCoinDenomination.value.comparedTo(pageStore.balanceOfCoin.value) > 0 ? (
                        <>
                          <Popover
                            placement="top"
                            trigger="click"
                            content={
                              <>
                                <div style={{ textAlign: 'center', fontWeight: 'bold', maxWidth: '12rem' }}>
                                  {lang.t('amount.invalid')}
                                </div>
                                <div
                                  style={{
                                    textAlign: 'center',
                                    fontWeight: 'bold',
                                    maxWidth: '12rem',
                                    pointerEvents: 'auto'
                                  }}
                                ></div>
                              </>
                            }
                            backgroundColor="#45bcb8"
                          >
                            <img src="/images/alert.png" style={{ cursor: 'pointer' }} alt="" />
                          </Popover>
                        </>
                      ) : null}
                    </div>

                    <div
                      className={styles.denomenation.className}
                      style={{
                        marginBottom: '0.5rem'
                      }}
                    >
                      <div
                        style={{
                          alignItems: 'center',
                          display: 'flex',
                          fontSize: '18px',
                          color: 'white'
                        }}
                      >
                        ETH&nbsp;{lang.t('Denomination')}:&nbsp;
                        {store.currentCoinDenomination.getFormat({ decimals: pageStore.currentChain.Coin.decimals })}
                      </div>
                    </div>
                  </BorderBox>
                )}
              </div>
            </Section>
            <Section css={{ width: '100%', '@md': { width: '46%' } }}>
              {/* <Button
                avaliable={
                  pageStore.isConnect &&
                  !store.cycApproved &&
                  new BigNumber(pageStore.balanceOfCYC.value).comparedTo('0') &&
                  store.currentCYCDenomination.value.comparedTo(0) > 0
                }
                label={lang.t('approve') + ' M87'}
                disabledLabel={lang.t('approved')}
                disable={store.cycApproved}
                onClick={store.onApproveCYC}

                LoadingButton
              /> */}
              <div style={{ flexDirection: 'column', justifyContent: 'space-between', height: '120px', display: 'flex' }}>
                <LoadingButton
                  color='success'
                  loadingPosition='start'
                  loading={store.ApproveStatus == "loading"}
                  disabled={store.ApproveStatus != "notApproved"}
                  variant="outlined"
                  onClick={store.onApproveCYC}
                  sx={{
                    height: '40px',
                    fontWeight: 'bold',
                    color: store.ApproveStatus != "notApproved" ? '#acb3bb !important' : '',
                    background: store.ApproveStatus != "notApproved" ? '#b9b9b952 !important' : ''
                  }}
                  fullWidth
                >
                  { store.ApproveStatus == "approved" ? "Approved" : "Approve M87"}
                </LoadingButton>

                <LoadingButton
                  color='success'
                  loadingPosition='start'
                  loading={store.DepositStatus == "loading"}
                  disabled={store.DepositStatus != "active"}
                  variant="outlined"
                  sx={{
                    height: '40px',
                    fontWeight: 'bold',
                    color: store.DepositStatus != "active" ? '#acb3bb !important' : '',
                    background: store.DepositStatus != "active" ? '#b9b9b952 !important' : ''
                  }}
                  onClick={store.deposit}
                  fullWidth
                >
                  Deposit
                </LoadingButton>
              </div>
             {/* <div className={styles.buttonPanel.className}>{ActionButton}</div> */}
            </Section>
          </FlexBox>
        </DropBox>
        <FlexBox justify="between" responsive="rc" style={{color: 'white', paddingLeft:'3%'}}>
          <Section css={{ width: '100%', mb: '1.5rem', '@md': { width: '46%', mb: 0 } }}>
              <div className={styles.poolInfos.className}>
                {lang.t('pool.size')}: &nbsp;{store.poolSize > -1 ? store.poolSize : '...'}
              </div>

              <div className={styles.poolInfos.className}>
                {lang.t('pool.contract')}:<br/>{(pageStore.CurrentSet.address)}
                <input
                  ref={node => (clipboardy = node)}
                  style={{
                    width: '1px',
                    height: '1px',
                    border: 'none',
                    backgroundColor: 'transparent',
                    padding: 0,
                    fontSize: '3px'
                  }}
                  value={pageStore.CurrentSet.address}
                />
              </div>
          </Section>
          <Section css={{ width: '100%', mb: '1.5rem', '@md': { width: '46%', mb: 0 } }}>
              <div className={styles.poolInfos.className}>
                {lang.t('pool.fee')}: &nbsp;{ pageStore.CurrentSet.feeOfProtocol }
              </div>

              <div className={styles.poolInfos.className}>
                {lang.t('relayer.fee')}: &nbsp;
                { pageStore.CurrentSet.feeOfRelayer }
              </div>

              {/* <div className="perShare">
                {lang.t('total.apy')}:&nbsp;
                <CountUp end={pageStore.curPoolApy} duration={2} decimals={2} preserveValue />%
              </div> */}
          </Section>
        </FlexBox>
      </div>
      );
    }
  });

  useEffect(() => {
    // console.log('props.chainID', props.chainID);
    store.init(props.chainID);
  }, []);

  useEffect(() => {
    store.isShowDetail = store.indexOfPool == pageStore.currentPoolIndex;
  }, [pageStore.currentPoolIndex]);

  useEffect(() => {
    // console.log('DepositStatus', store.DepositStatus);
    if(store.DepositStatus == "loading")
      return;
    if( store.allowDeposit )
      store.DepositStatus = "active";
    else
      store.DepositStatus = 'notActive';
  }, [ store.allowDeposit, baseStore.refetchTimer ]);

  useEffect(() => {
        // console.log("Approve consider");
        if(store.ApproveStatus == "loading")
          return;
        // console.log(store.ApproveStatus);
        if( store.cycApproved )
        {
          store.ApproveStatus = "approved";
          // console.log('1');
        }
        else {
          // console.log('2'); 
          const isActive = pageStore.isConnect &&
            new BigNumber(pageStore.balanceOfCYC.value).comparedTo('0') &&
            store.currentCYCDenomination.value.comparedTo(0) > 0;
          if( isActive )
            store.ApproveStatus = "notApproved";
          else
            store.ApproveStatus = "disabled";
        }
  }, [pageStore.isConnect, store.cycApproved, pageStore.balanceOfCYC.value, store.currentCYCDenomination ]);

  useEffect(() => {
    // console.log('props.chainID =', props.chainID);

    store.init(props.chainID);
  }, [props.chainID, pageStore.web3, pageStore.account]);

  useEffect(() => {
    // console.log('timer', baseStore.refetchTimer);
    store.loadAmount();
  }, [baseStore.refetchTimer]);

  return (
    <>
    { store.indexOfPool &&
      <Section>
        <BorderBox css={{ padding: '0.6rem 0', cursor: 'pointer' }}
        onClick={
          debounce(() => {
              store.onSelectPool(); 
          }, 300)
        }>
          <FlexBox justify="between">
            <FlexBox css={{ width: 'max-content', cursor: 'pointer', '@md': { width: '32%' } }}>
              <SingleSymbol imgs={['/images/home/token_eth.png']} />
              <Text size="small" color="primary" css={{ ml: '0.8rem' }}>
                <span>
                  {store.tokens &&
                      store.tokens[store.indexOfPool].set
                  }
                </span>
              </Text>
            </FlexBox>
            <Text size="small" color="white" css={{ flex: 1, textAlign: 'right', cursor: 'pointer' }}>
              { (store.indexOfPool == 1 || store.indexOfPool == 10) && store.tokens[store.indexOfPool].Apy.format + ' % / ' + store.tokens[store.indexOfPool].dailyApy.format + ' %' }
            </Text>
            <FlexBox justify="end" css={{ width: 'max-content', cursor: 'pointer', '@md': { width: '40%' } }}>
              <Text size="small" color="white" css={{ display: 'none', '@md': { display: 'block' } }}>
                ${store.tokens[store.indexOfPool].tvl.format}
              </Text>
              <Icon
                css={{ width: '1rem', ml: '1rem', '@md': { width: '1.375rem', ml: '1.5rem' } }}
                src={`/images/home/${store.isShowDetail ? 'dropup' : 'dropdown'}.png`}
              ></Icon>
            </FlexBox>
          </FlexBox>
        </BorderBox>
        {AnonymityPool}
      </Section>
    }
    </>
  );
});

